import { Link } from "react-router-dom";
import {
    Formik,
    Form,
    Field,
    ErrorMessage as FormicErrorMessage,
} from "formik";
import * as Yup from "yup";
import "./appForm.scss";
import { useEffect, useState } from "react";
import useMarvelService from "../services/MarvelServic";

const AppForm = () => {
    const [charactersData, setCharactersData] = useState([]); // –º–∞—Å—Å–∏–≤ –ø–µ—Ä—Å–æ–Ω–∞–∂–µ–π
    const [searchResult, setSearchResult] = useState(null); // –î–æ–±–∞–≤–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ –ø–æ–∏—Å–∫–∞
    const [foundCharacter, setFoundCharacter] = useState(null); // –î–æ–±–∞–≤–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞

    const { getAllCharacters } = useMarvelService();

    useEffect(() => {
        const loadCharacters = async () => {
            try {
                const data = await getAllCharacters("offset", 20);
                if (data) setCharactersData(data);
                else console.error("No data received from API");
            } catch (err) {
                console.error("Failed to load comics:", err);
            }
        };
        loadCharacters();
    }, []);

    const handleSubmit = (values, { setSubmitting }) => {
        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ –ø–µ—Ä—Å–æ–Ω–∞–∂ —Å —Ç–∞–∫–∏–º –∏–º–µ–Ω–µ–º
        const searchCharacter = charactersData.find(
            (char) => char.name.toLowerCase() === values.search.toLowerCase(),
        );

        if (searchCharacter) {
            console.log("super - –ø–µ—Ä—Å–æ–Ω–∞–∂ –Ω–∞–π–¥–µ–Ω");
            setSearchResult(`There is! Visit ${values.search} page?`);
            setFoundCharacter(searchCharacter); // –°–æ—Ö—Ä–∞–Ω—è–µ–º –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞
        } else {
            console.log("bad - –ø–µ—Ä—Å–æ–Ω–∞–∂ –Ω–µ –Ω–∞–π–¥–µ–Ω");
            setSearchResult(
                `The character was not found. Check the name and try again`,
            );
            setFoundCharacter(null); // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –ø—Ä–∏ –æ—Ç—Å—É—Ç—Å—Ç–≤–∏–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞
        }

        setSubmitting(false);
    };

    return (
        <Formik
            initialValues={{
                search: "",
            }}
            validationSchema={Yup.object({
                search: Yup.string().required("This field is required"),
            })}
            onSubmit={handleSubmit}
        >
            <Form className="appform">
                <label className="appform__name" htmlFor="search">
                    Or find a character by name:
                </label>
                <div className="appform-wrapper">
                    <Field
                        id="search"
                        className="appform__input"
                        name="search"
                        type="text"
                        placeholder="Enter name"
                    />

                    <div className="randomChar__content-buttonBlock">
                        <button type="submit" className="button button__main">
                            <div className="inner">FIND</div>
                        </button>
                    </div>
                </div>
                <FormicErrorMessage
                    className="error"
                    name="search"
                    component="div"
                />

                <div className="appform__message">
                    {searchResult && (
                        <>
                            <div
                                style={{
                                    color: foundCharacter
                                        ? "#03710e"
                                        : "#9f0013",
                                    paddingTop: "25px",
                                    fontWeight: "700",
                                    fontSize: "18px",
                                }}
                                className="search-result"
                            >
                                {searchResult}
                            </div>
                            {foundCharacter && (
                                <Link
                                    to={`/characters/${foundCharacter.id}`}
                                    className="button button__secondary"
                                >
                                    <div className="inner">TO PAGE</div>
                                </Link>
                            )}
                        </>
                    )}
                </div>
            </Form>
        </Formik>
    );
};

export default AppForm;
