import { useEffect, useState, createRef } from "react";
import useMarvelService from "../services/MarvelServic";
import Spinner from "../spinner/Spinner";
import ErrorMessage from "../errorMessage/ErrorMessage";
import PropTypes from "prop-types";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import "./charList.scss";

const CharList = (props) => {
    const [char, setChar] = useState([]);
    const [newItemLoading, setNewItemLoading] = useState(false);
    const [offset, setOffset] = useState(0);

    const [charEnded, setCharEnded] = useState(false);

    const { loading, error, getAllCharacters } = useMarvelService();

    useEffect(() => {
        upDateChars(); //–Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ 9 –ø–µ—Ä—Å–æ–Ω–∞–∂–µ–π
    }, []);

    const upDateChars = () => {
        getAllCharacters(offset).then((res) => setChar(res), setOffset(9));
    };

    const onRequest = (offset, initial) => {
        //–¥–æ–∑–∞–≥—Ä—É–∑–∫–∞
        initial ? setNewItemLoading(false) : setNewItemLoading(true);

        getAllCharacters(offset)
            .then((newChar) => {
                let ended = false;

                if (newChar.length < 9 || newChar.length === 0) {
                    ended = true;
                }
                setChar((char) => {
                    const upDateChar = [...char, ...newChar];
                    return upDateChar;
                });

                setOffset((offset) => offset + 9);
                setCharEnded(ended);
            })

            .catch((err) => {
                console.error("–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –ø–µ—Ä—Å–æ–Ω–∞–∂–µ–π:", err); // üö® –õ–æ–≥–∏—Ä—É–µ–º –æ—à–∏–±–∫—É
            });
    };
    const duration = 1000;

    const onFocusItem = (ref) => {
        ref.current.classList.add("charList__selected");
        ref.current.focus();
    };

    const blurOnItem = (ref) => {
        ref.current.classList.remove("charList__selected");
    };

    const errorMessage = error ? <ErrorMessage /> : null;
    const spinner = loading && !newItemLoading ? <Spinner /> : null;
    const charitem = char.map((item, i) => {
        const { thumbnail, name, id } = item;
        const nodeRef = createRef(null);

        return (
            <CSSTransition
                in={true}
                key={id}
                timeout={duration}
                classNames="char-anim"
                nodeRef={nodeRef}
            >
                <li
                    ref={nodeRef}
                    tabIndex={0}
                    className="charList__flex-item"
                    onClick={() => {
                        props.onCharSelected(item.id);
                        onFocusItem(nodeRef);
                    }}
                    onBlur={() => blurOnItem(nodeRef)}
                    onKeyDown={(e) => {
                        if (e.key === " " || e.key === "Enter") {
                            props.onCharSelected(item.id);
                            onFocusItem(nodeRef);
                        }
                    }}
                >
                    <img
                        src={thumbnail}
                        alt={name}
                        className="charList__flex-img"
                    />
                    <div className="charList__flex-name">{name}</div>
                </li>
            </CSSTransition>
        );
    });

    return (
        <section className="charList">
            <ul className="charList__flex spinner error">
                {spinner}
                {errorMessage}
                <TransitionGroup component={null} className="charList__flex">
                    {charitem}
                </TransitionGroup>
            </ul>
            <div className="randomChar__content-buttonBlock">
                <button
                    className="button button__long button__main"
                    disabled={loading} //–∫–Ω–æ–ø–∫–∞ –±–ª–æ–∫–∏—Ä–æ–≤–∞–ª–∞—Å—å –≤–æ –≤—Ä–µ–º—è –∑–∞–≥—Ä—É–∑–∫–∏
                    style={{ display: charEnded ? "none" : "block" }}
                    onClick={() => onRequest(offset)}
                >
                    <div className="inner">LOAD MORE</div>
                </button>
            </div>
        </section>
    );
};

export default CharList;

CharList.propTypes = {
    onCharSelected: PropTypes.func.isRequired,
};

